export default class RetryablePromise<TResult> implements Promise<TResult> {
  private static _setTimeout: (cb: () => void, timeout: number) => void = setTimeout;
  private _innerPromise: Promise<TResult>;

  public static setTimeoutMock(setTimeout: (cb: () => void, timeout: number) => void): void {
    RetryablePromise._setTimeout = setTimeout;
  }

  public static allWithRetries<T>(
    promises: RetryablePromise<T>[],
    maxRetries: number,
    retryDelayInMs: number = 0
  ): Promise<T[]> {
    return new Promise<T[]>((resolve: (value: T[]) => void, reject: (error: Error) => void) => {
      let successCount: number = 0;
      const results: T[] = [];
      const failureCounts: number[] = [];

      function attachPromiseHandler(promise: RetryablePromise<T>, index: number): void {
        promise.then((value: T) => {
          results[index] = value;
          successCount++;

          if (successCount === promises.length) {
            resolve(results);
          }
        }).catch(() => {
          if (maxRetries > 0 && (failureCounts[index] === undefined || failureCounts[index] < maxRetries)) {
            failureCounts[index] = !failureCounts[index] ? 1 : failureCounts[index] + 1;
            if (!retryDelayInMs) {
              attachPromiseHandler(promise.retry(), index);
            } else {
              RetryablePromise._setTimeout(() => attachPromiseHandler(promise.retry(), index), retryDelayInMs);
            }
          } else {
            reject(new Error(`Maximum retries (${maxRetries}) exceeded.`));
          }
        });
      }

      for (let i: number = 0; i < promises.length; i++) {
        attachPromiseHandler(promises[i], i);
      }
    });
  }

  constructor(private _promiseFn: Promise<TResult> |
    ((resolve: (value?: TResult) => void, reject: (error: Error) => void) => void)) {

    if (typeof _promiseFn === 'function') {
      this._innerPromise = new Promise(_promiseFn);
    } else {
      this._innerPromise = _promiseFn;
    }
  }

  public retry(): RetryablePromise<TResult> {
    return new RetryablePromise(this._promiseFn);
  }

  public then<TReturn>(
    onFulfilled?: (value: TResult) => TReturn | Thenable<TReturn>,
    onRejected?: (error: Error) => TReturn | Promise<TReturn> | void
  ): Promise<TReturn> {
    return this._innerPromise.then(onFulfilled, onRejected);
  }

  public catch<TReturn>(onRejected?: (error: Error) => TReturn | Thenable<TReturn>): Promise<TReturn> {
    return this._innerPromise.catch(onRejected);
  }
}
